.homesecWrap {
    .article1 {
        height: 50vw !important;
    }

    .article2 {
        .row {
            ul {
                display: flex;
                flex-wrap: wrap;
                li {
                    width: 33.33%;
                    padding: 20px;
                    a { 
                        display: flex;
                        img {
                            width: 30%;
                            margin-right: 10px;
                            border-radius: 10px;
                        }
                        div {
                            align-self: center;
                        }
                    }
                }
            }
            .lifeStyle {
                h2 {
                    font-size: 20px;
                }
                ul {
                    display: flex;
                    li {
                        width: 33.33%;
                        img {
                            width: 100%;
                            border-radius: 20px;
                        }
                    }
                }
                
            }
        }
    }

    .article3 {
        background: #000;
        color: #fff;
        padding: 100px 0;
        .row {
            display: flex;
            div {
                width: 52%;
                position: relative;
                img {
                    width: 100%;
                    padding: 10px 10px 0;
                    border-radius: 30px 30px 0 0;
                    vertical-align: middle;
                }
                p {
                    background: rgb(139, 131, 253);
                    margin: 0 10px;
                    padding: 20px;
                    border-radius: 0 0 30px 30px;
                }
                &.right {
                    width: 48%;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    div {
                        width: 100%;
                    }
                    .rightFst{
                        display: flex;
                        flex-direction: row;
                    }
                }
            }
        }
    }
}