.headerWrap {
    position: fixed;
    top: 0; left: 0;
    width: 100%;
    color: #fff;
    z-index: 999999;
    transition: all 0.5s;
    .row {
        display: flex;
        height: 80px;
        justify-content: space-between;
        position: relative;
        align-items: center;
        .mobileNav {
            position: relative;
            button {
                padding: 10px 20px;
                border-radius: 50px;
                border: none;
                font-size: 20px;
                display: flex;
                cursor: pointer;
                .ham {
                    margin-right: 10px;
                }
                .ham, .person {
                    align-self: center;
                }
            }
            .mobileNavList {
                display: none;
                position: absolute;
                top: 100%; right: 0;
                margin-top: 10px;
                background: #fff;
                padding: 10px 0;
                color: #000;
                width: 150px;
                border-radius: 10px;
                &.opener {
                    display: block;
                }
                ul {
                    li {
                        line-height: 30px;
                        padding: 0 20px;
                    }
                    li:nth-child(3) {
                        border-top: 1px solid #ddd;
                        padding-top: 10px;
                        margin-top: 10px;
                    }
                }
            }
        }
        .nav {
            position: absolute;
            top: 50%; left: 50%;
            transform: translate(-50%, -50%);
            .depth1 {
                li {
                    display: inline-block;
                    position: relative;
                    padding: 10px 20px;
                }
            }
            .depth2 {
                display: none;
                position: absolute;
                top: 100%; left: 0;
                width: 150px;
                background: #fff;
                color: #000;
                border-radius: 10px;
                font-size: 14px;
                padding: 10px 10px;
                font-weight: bold;
                &.on {
                    display: block;
                }
            }
        }
    }
}
