.productWrap {
    height: 2000px;
    .article1 {
        padding-top: 100px;
        height: 45vw !important;
        .bg {
            height: 90%;
            background: url(productimg1.jpg) no-repeat center center;
            background-size: cover;
            color: #fff;
            h3 {
                font-size: 48px;
                padding: 200px 0 40px;
            }
            p {
                font-size: 18px;
                line-height: 1.5;
            }
        }
    }
    .article2 {
        padding-bottom: 100px;
        h6 {
            font-size: 20px;
            padding-bottom: 40px;
        }
        div {
            .icon {
                display: inline-block;
                width: 14.28%;
                padding: 0 10px;
                img {
                    width: 100%;
                    border-radius: 20px 20px 0 0;
                    background: #fff;
                    box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px;
                    vertical-align: middle;
                }
                p {
                    font-size: 14px;
                    font-weight: bold;
                    padding: 10px;
                    border-radius: 0 0 20px 20px;
                    background: #fff;
                    box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px;
                }
            }
        }
    }
    .article3{
        h6 {
            font-size: 20px;
        }
        p {
            line-height: 1.7;
            padding-bottom: 15px;
        }
        .host {
            padding: 10px;
            width: 25%;
            display: inline-block;
            .imgbox {
                position: relative;
                span {
                    position: absolute;
                    top: 10px;
                    left: 10px;
                    background: #fff;
                    padding: 5px 10px;
                    font-size: 12px;
                    border-radius: 15px;
                }
                img {
                    width: 100%;
                    border-radius: 20px;
                }
            }
            .score {
                padding: 10px 0 5px;
                font-size: 13px;
                span {
                    color: #ff385c;
                }
            }
            p {
                font-size: 14px;
            }
        }
    }
}